<template>
    <div>
        <h2 class="mt-3">Events</h2>
        <div v-if="$store.getters.events.length > 0" class="border rounded">
            <div :key="key" v-for="(event, key) in $store.getters.events">
                <p class="text-dark p-3 mb-0" v-bind:class="{'bg-light': key % 2 === 0}">{{ getText(event) }}</p>
            </div>
        </div>
        <div v-else class="mt-5 d-flex justify-content-center w-100 mb-5">
            <Spinner/>
        </div>
    </div>
</template>

<script>
	import Spinner from './Spinner'

	export default {
		components: {
			Spinner
		},
		props: {
			fakeTimeTillNextGame: Number
		},
        methods: {
			getText: function(event) {
				return event.replace('%seconds%', this.fakeTimeTillNextGame < 0 ? 0 : this.fakeTimeTillNextGame)
            }
        }
	}
</script>