<template>
    <div id="app" class="container">
        <div class="row mt-5">
            <div class="col-3">
                <Stats ref="stats" :color-mapping="colorMapping"/>
            </div>
            <div class="col-9">
                <GameBoard :color-mapping="colorMapping"/>
                <Events :fake-time-till-next-game="fakeTimeTillNextGame"/>
                <Logger/>
            </div>
        </div>
    </div>
</template>

<script>
	import api from './api'
	import Stats from './components/Stats'
	import GameBoard from './components/GameBoard'
	import Events from './components/Events'
	import Logger from './components/Logger'

	export default {
		name: 'App',
		components: {
			Stats,
			GameBoard,
			Events,
			Logger
		},
		data: function () {
			return {
				nextGameId: null,
				fakeTimeTillNextGame: null,
				timerInterval: null,
				timeToWaitBeforeFetchingGameResults: null,
				colorMapping: {
					green: 'success',
					black: 'dark',
					red: 'danger'
				}
			}
		},
		methods: {
			getConfiguration: function (delayTime = 0) {
				const delayAfterFailedAttempt = 2000
				setTimeout(() => {
					this.$store.dispatch('addLog', 'GET .../' + api.configuration)
					this.axios.get(api.configuration).then(response => {
						if (response.data && response.data.colors && response.data.results) {
							let slots = response.data.colors.map((value, key) => {
								let id = response.data.positionToId[key]
								return {
									color: response.data.colors[id],
									number: parseFloat(response.data.results[id])
								}
							})
							this.$store.dispatch('addLog', 'REQUEST SUCCESS GET .../' + api.configuration)
							this.$store.dispatch('setSlots', slots)
						} else {
							this.$store.dispatch('addLog', 'REQUEST FAIL GET .../' + api.configuration + '. TRYING AGAIN IN ' + delayAfterFailedAttempt / 1000 + 'seconds')
							this.getConfiguration(delayAfterFailedAttempt)
						}
					}).catch(() => {
						this.$store.dispatch('addLog', 'REQUEST FAIL GET .../' + api.configuration + '. TRYING AGAIN IN ' + delayAfterFailedAttempt / 1000 + 'seconds')
						this.getConfiguration(delayAfterFailedAttempt)
					}), delayTime
				})
			},
			getNextGame: function (delayTime = 0) {
				const delayAfterFailedAttempt = 2000
				setTimeout(() => {
					this.$store.dispatch('addLog', 'GET .../' + api.nextGame)
					this.axios.get(api.nextGame).then(response => {
						if (response.data && response.data.fakeStartDelta && response.data.startDelta && response.data.id) {
							this.fakeTimeTillNextGame = response.data.fakeStartDelta
							this.timeToWaitBeforeFetchingGameResults = response.data.startDelta - response.data.fakeStartDelta
							this.nextGameId = response.data.id
							this.$store.dispatch('addLog', 'REQUEST SUCCESS GET .../' + api.nextGame)
						} else {
							this.$store.dispatch('addLog', 'REQUEST FAIL GET .../' + api.nextGame + '. TRYING AGAIN IN ' + delayAfterFailedAttempt / 1000 + 'seconds')
							this.getNextGame(delayAfterFailedAttempt)
						}
					}).catch(() => {
						this.$store.dispatch('addLog', 'REQUEST FAIL GET .../' + api.nextGame + '. TRYING AGAIN IN ' + delayAfterFailedAttempt / 1000 + 'seconds')
						this.getNextGame(delayAfterFailedAttempt)
					})
				}, delayTime)
			},
			getGameResults: function (delayTime) {
				const delayAfterFailedAttempt = 2000
				setTimeout(() => {
					this.$store.dispatch('addLog', 'GET .../' + api.nextGameId)
					this.axios.get(api.game + this.nextGameId).then(response => {
						if (response.data.result) {
							this.$store.dispatch('replaceLastEvent', 'Game ' + this.nextGameId + ' ended, result is ' + response.data.result)
							this.$refs.stats.getStats()
							this.getNextGame()
							this.$store.dispatch('addLog', 'REQUEST SUCCESS GET .../' + api.nextGameId)
						} else {
							this.$store.dispatch('addLog', 'REQUEST FAIL GET .../' + api.nextGameId + '. TRYING AGAIN IN ' + delayAfterFailedAttempt / 1000 + 'seconds')
							this.getGameResults(delayAfterFailedAttempt)
						}
					}).catch(() => {
						this.$store.dispatch('addLog', 'REQUEST FAIL GET .../' + api.nextGameId + '. TRYING AGAIN IN ' + delayAfterFailedAttempt / 1000 + 'seconds')
						this.getGameResults(delayAfterFailedAttempt)
					})
				}, delayTime ?? this.timeToWaitBeforeFetchingGameResults * 1000)
			},
			setTimeLeftTillNextGame: function (firstTime) {
				let timeLeft = this.fakeTimeTillNextGame - (firstTime ? 0 : 1)
				this.fakeTimeTillNextGame = firstTime && timeLeft < 0 ? 0 : timeLeft
			}
		},
		created () {
			this.getConfiguration()
			this.getNextGame()
		},
		watch: {
			nextGameId () {
				this.setTimeLeftTillNextGame(true)
				this.timerInterval = setInterval(() => this.setTimeLeftTillNextGame(), 1000)
				this.$store.dispatch('addEvent', 'Game ' + this.nextGameId + ' will start in %seconds% sec')
			},
			fakeTimeTillNextGame () {
				if (this.fakeTimeTillNextGame === 0) {
					this.$store.dispatch('replaceLastEvent', 'Game ' + this.nextGameId + ', the wheel is spinning')
					clearInterval(this.timerInterval)
					this.getGameResults()
				}
			}
		}
	}
</script>

<style lang="scss">
    .slot {
        color: #ffffff;
        padding: 5px;

        &--red {
            background-color: red;
        }

        &--black {
            background-color: black;
        }

        &--green {
            background-color: green;
        }
    }
</style>