import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
	state: {
		slots: [],
		events: [],
		logger: []
	},
	mutations: {
		SET_SLOTS(state, value) {
			state.slots = value
		},
		SET_EVENTS(state, value) {
			state.events = value
		},
		'ADD_LOG'(state, value) {
			state.logger.push(value);
		}
	},
	actions: {
		setSlots({ commit }, value) {
			commit('SET_SLOTS', value);
		},
		addEvent({ commit, state }, value) {
			commit('SET_EVENTS', [...state.events.slice(), value]);
		},
		replaceLastEvent({ commit, state }, value) {
			commit('SET_EVENTS', [...state.events.slice(0, state.events.length - 1), value]);
		},
		addLog({ commit }, value) {
			let timeStamp = (new Date()).toISOString();
			commit('ADD_LOG', timeStamp + ' ' + value);
		}
	},
	getters: {
		slots: state => state.slots,
		slotByNumber: state => number => state.slots.find(slot => slot.number === number),
		events: state => state.events,
		logger: state => state.logger
	}
});