<template>
    <div>
        <h5>Stats(last {{ statsBasedOnLastGames }})</h5>
        <template v-if="stats && stats.length > 0 && $store.getters.slots.length > 0">
            <div class="d-flex justify-content-around">
                <h5>Slot</h5>
                <h5>Hits</h5>
            </div>
            <div :key="slot.result" v-for="(slot, key) in stats"
                 :class="'bg-' + getStatsBackgroundModifier(key)">
                <h5 v-if="key === 0" class="text-center pt-3">Cold</h5>
                <h5 v-if="key === numberOfColds" class="text-center pt-3">Neutral</h5>
                <h5 v-if="key === stats.length - numberOfHots" class="text-center mt-3 pt-3">Hot</h5>
                <div class="d-flex justify-content-around align-items-center">
                    <div :class="'bg-' + findColorBySlotNumber(slot.result)"
                         class="text-center p-1 rounded text-white stats-slot">{{ slot.result }}
                    </div>
                    <div class="text-dark">{{ slot.count }}</div>
                </div>
            </div>
        </template>
        <div v-else class="mt-5 d-flex justify-content-center">
            <Spinner/>
        </div>
    </div>
</template>

<script>
	import api from '../api'
	import Spinner from './Spinner'

	export default {
		components: {
			Spinner
		},
		data: function () {
			return {
				statsBasedOnLastGames: 200,
				numberOfColds: 5,
				numberOfHots: 5,
				stats: null
			}
		},
		props: {
			colorMapping: Object
		},
		methods: {
			getStatsBackgroundModifier: function (key) {
				return key < this.numberOfColds ?
                    'cold' :
                    this.$store.getters.slots.length - this.numberOfHots < key + 1 ?
                        'hot' :
                        ''
			},
			findColorBySlotNumber: function (number) {
				if (!this.$store.getters.slots) return ''
				let slot = this.$store.getters.slotByNumber(number);
				return slot ? this.colorMapping[slot.color] : ''
			},
			getStats: function (delayTime = 0) {
				this.stats = null
				const delayAfterFailedAttempt = 2000
				setTimeout(() => {
					let url = api.stats + '?limit=' + this.statsBasedOnLastGames;
					this.$store.dispatch('addLog', 'GET .../' + url)
					this.axios.get(url).then(response => {
						if(response.data) {
							this.$store.dispatch('addLog', 'REQUEST SUCCESS GET .../' + url)
							this.stats = response.data
						} else {
							this.$store.dispatch('addLog', 'REQUEST FAIL GET .../' + url + '. TRYING AGAIN IN ' + delayAfterFailedAttempt / 1000 + 'seconds')
							this.getStats(delayAfterFailedAttempt)
						}
					}).catch(() => {
						this.$store.dispatch('addLog', 'REQUEST FAIL GET .../' + url + '. TRYING AGAIN IN ' + delayAfterFailedAttempt / 1000 + 'seconds')
						this.getStats(delayAfterFailedAttempt)
					})
				}, delayTime)
			}
		},
		created () {
			this.getStats()
		}
	}
</script>

<style lang="scss">
    .bg {
        &-cold {
            background-color: skyblue;
        }

        &-hot {
            background-color: lightpink;
        }
    }
    .stats-slot {
        width: 50px;
    }
</style>